import Vue from 'vue'
import Router from 'vue-router'
import MainPage from '@/components/MainPage'
import IntroducePage from '@/components/IntroducePage'
import IntroCompany from '@/components/IntroCompany'
import IntroLocation from '@/components/IntroLocation'
import ChemistrylawPage from '@/components/ChemistrylawPage'
import Chemistrylaw1 from '@/components/Chemistrylaw1'
import Chemistrylaw2 from '@/components/Chemistrylaw2'
import Chemistrylaw3 from '@/components/Chemistrylaw3'
import Chemistrylaw4 from '@/components/Chemistrylaw4'
import Chemistrylaw5 from '@/components/Chemistrylaw5'
import Chemistrylaw6 from '@/components/Chemistrylaw6'
import Chemistrylaw7 from '@/components/Chemistrylaw7'
import ConfirmsystemPage from '@/components/ConfirmsystemPage'
import Confirmsystem1 from '@/components/Confirmsystem1'
import Confirmsystem2 from '@/components/Confirmsystem2'
import MSDSPage from '@/components/MSDSPage'
import MSDS1 from '@/components/MSDS1'
import MSDS2 from '@/components/MSDS2'
import RegistrationPage from '@/components/RegistrationPage'
import Registration1 from '@/components/Registration1'
import Registration2 from '@/components/Registration2'
import Registration3 from '@/components/Registration3'
import AdminPage from '@/components/AdminPage'
import EditPage from '@/components/EditPage'
import PostingPage from '@/components/PostingPage'
import CommunityPage from '@/components/CommunityPage'
import Community1 from '@/components/Community1'
import Community1_1 from '@/components/Community1_1'
import Community2 from '@/components/Community2'
import Community2_1 from '@/components/Community2_1'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Main',
      component: MainPage,
      meta: {
        title: 'main'
      }
    },
    {
      path: '/introduce',
      redirect: '/introduce/intro',
      name: 'Introduce',
      component: IntroducePage,
      children: [
        {
          path: 'intro',
          component: IntroCompany,
          meta: {
            title: '회사소개',
            sub: '회사소개'
          }
        },
        {
          path: 'location',
          component: IntroLocation,
          meta: {
            title: '회사소개',
            sub: '오시는길'
          }
        }
      ]
    },
    {
      path: '/chemistrylaw',
      redirect: '/chemistrylaw/chemistrylaw',
      name: 'chemistrylaw',
      component: ChemistrylawPage,
      children: [
        {
          path: 'chemistrylaw',
          component: Chemistrylaw1,
          meta: {
            title: '화평법',
            sub: '화평법'
          }
        },
        {
          path: 'beforedeclaration',
          component: Chemistrylaw2,
          meta: {
            title: '화평법',
            sub: '기존화학물질사전신고'
          }
        },
        {
          path: 'beforesignup',
          component: Chemistrylaw3,
          meta: {
            title: '화평법',
            sub: '기존화학물질등록'
          }
        },
        {
          path: 'newchemi',
          component: Chemistrylaw4,
          meta: {
            title: '화평법',
            sub: '신규화학물질신고등록'
          }
        },
        {
          path: 'exemption',
          component: Chemistrylaw5,
          meta: {
            title: '화평법',
            sub: '신고등록면제'
          }
        },
        {
          path: 'polymercompound',
          component: Chemistrylaw6,
          meta: {
            title: '화평법',
            sub: '고분자화합물'
          }
        },
        {
          path: 'intermediate',
          component: Chemistrylaw7,
          meta: {
            title: '화평법',
            sub: '중간체'
          }
        }
      ]
    },
    {
      path: '/confirmsystem',
      redirect: '/confirmsystem/confirmsystem1',
      name: 'confirmsystem',
      component: ConfirmsystemPage,
      children: [
        {
          path: 'confirmsystem1',
          component: Confirmsystem1,
          meta: {
            title: '확인제도',
            sub: '화학물질확인신고'
          }
        },
        {
          path: 'confirmsystem2',
          component: Confirmsystem2,
          meta: {
            title: '확인제도',
            sub: '인벤토리'
          }
        }
      ]
    },
    {
      path: '/msds',
      redirect: '/msds/msds1',
      name: 'MSDS',
      component: MSDSPage,
      children: [
        {
          path: 'msds1',
          component: MSDS1,
          meta: {
            title: 'MSDS',
            sub: 'MSDS의작성및제출'
          }
        },
        {
          path: 'msds2',
          component: MSDS2,
          meta: {
            title: 'MSDS',
            sub: 'MSDS의일부비공개승인'
          }
        }
      ]
    },
    {
      path: '/registration',
      redirect: '/registration/registration1',
      name: 'registration',
      component: RegistrationPage,
      children: [
        {
          path: 'registration1',
          component: Registration1,
          meta: {
            title: '국내외등록',
            sub: '산안법신규화학물질'
          }
        },
        {
          path: 'registration2',
          component: Registration2,
          meta: {
            title: '국내외등록',
            sub: '국외등록'
          }
        },
        {
          path: 'registration3',
          component: Registration3,
          meta: {
            title: '국내외등록',
            sub: '기타서비스'
          }
        }
      ]
    },
    {
      path: '/community',
      redirect: '/community/community1',
      name: 'community',
      component: CommunityPage,
      children: [
        {
          path: 'community1',
          component: Community1,
          meta: {
            title: '커뮤니티',
            sub: '공지사항'
          },
          children: [
            {
              path: 'post/:id',
              component: Community1_1,
              meta: {
                title: '포스트',
                sub: '공지사항'
              },
            }
          ]
        },
        {
          path: 'community2',
          component: Community2,
          meta: {
            title: '커뮤니티',
            sub: '자료실'
          },
          children: [
            {
              path: 'post/:id',
              component: Community2_1,
              meta: {
                title: '포스트',
                sub: '자료실'
              },
            }
          ]
        }
      ]
    },
    {
      path: '/admin',
      name: 'admin',
      component: AdminPage,
      meta: {
        title: 'admin'
      },
      beforeEnter: (to, from, next) => {
        if(window.localStorage.getItem('accessToken')) {
          next('/edit')
        }
        else next();
      }
    },
    {
      path: '/edit',
      name: 'edit',
      component: EditPage,
      meta: {
        title: 'admin'
      },
      beforeEnter: (to, from, next) => {
        if(!window.localStorage.getItem('accessToken')) {
          next('/admin')
        }
        else next();
      }
    },
    {
      path: '/posting/:mode',
      name: 'posting',
      component: PostingPage,
      meta: {
        title: 'admin'
      },
      beforeEnter: (to, from, next) => {
        if(!window.localStorage.getItem('accessToken')) {
          next('/admin')
        }
        else next();
      }
    },
    {
      path: '/posting/:mode/:category',
      name: 'posting',
      component: PostingPage,
      meta: {
        title: 'admin'
      },
      beforeEnter: (to, from, next) => {
        if(!window.localStorage.getItem('accessToken')) {
          next('/admin')
        }
        else next();
      }
    }
  ],
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
})



