<template>
    <div class="contents-box">
        <div class="title-wrap">
            <div class="bar"></div>
            <h1 class="title">기존화학물질 등록</h1>
        </div>
        <div class="item-wrap">
            <p class="text">기존화학물질 등록의무자는 공동등록 협의체를 통해 등록신청자료를 공동제출하는 것이 기본 원칙 입니다.</p>
            <div class="img-row">
                <img class="img4" src="@/assets/img/chemilaw_img4.png" />
                <img class="img5" src="@/assets/img/chemilaw_img5.png" />
            </div>
            <div class="item-box">
                <div class="row">
                    <List 
                        title="제공 서비스" 
                        :contents="[
                            '등록대상 여부 판단',
                            '대표자 등록, 기존화학물질 등록',
                            '협의체 운영 관리',
                            '데이터 공유 및 협상',
                            '위해성평가 보고서 작성',
                            '시험의뢰 대행 및 모니터링',
                            '유일대리인(OR)',
                        ]"
                    />
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import List from './StyledList';
export default {
    name: 'Chemistrylaw3',
    components: {
        List
    }
}
</script>



