<template>
    <div class="admin_page">
        <Loading v-if="loading" />
        <div class="top_bar">
            <router-link to="/"><img src="@/assets/img/logo_img_h_w.png" /></router-link>
            <span class="btn" @click="logout">LOGOUT</span>
        </div>
        <div class="ad_contents_wrap">
            <div class="ad_board_wrap">
                <div class="col">
                    <div class="title_area">
                        <span class="title">게시물 
                            <span v-if="mode=='write'">작성</span>
                            <span v-else-if="mode=='modify'">수정</span>
                        </span>
                        <span @click="submit" class="btn" style="font-size: 15px; letter-spacing: 1px">완료</span>
                    </div>
                    <div class="board_area">
                        <div class="title-row">
                            <select v-if="mode=='write'" v-model="post_category" required>
                                <option disabled selected value="">게시판 선택</option>
                                <option value="1">공지사항</option>
                                <option value="2">자료실</option>
                            </select>
                            <select v-else-if="mode=='modify'" v-model="post_category" required>
                                <option v-if="post_category=='1'" value="1">공지사항</option>
                                <option v-else-if="post_category=='2'" value="2">자료실</option>
                            </select>
                            <input v-model="post_title" class="input" placeholder="제목을 작성하세요."/>
                        </div>
                        <VueEditor
                            placeholder="본문을 작성하세요."
                            v-model="post_content"
                            :editorOptions="editorOption"
                            @ready="onEditorReady($event)"
                        />
                        <!-- <div class="ql-snow">
                            <div class="ql-editor">
                                <span v-html="content"></span>
                            </div>
                        </div> -->
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { VueEditor, Quill } from 'vue2-editor'
import ImageResize from '@taoqf/quill-image-resize-module'
import Loading from './Loading'

Quill.register('modules/imageResize', ImageResize)

const BaseURI = 'http://www.chemipartner.com'

export default {
    name: 'PostingPage',
    components: {
        VueEditor,
        Loading
    },
    data() {
        return {
            loading: false,

            mode: this.$route.params.mode,

            post_category: '',
            post_title: '',
            post_date: '',
            post_content: '',

            editorOption: {
                modules: {
                    imageResize: {}
                },
                toolbar : [
                    ['bold', 'italic', 'underline', 'strike'],        // toggled buttons
                    ['blockquote', 'code-block'],

                    [{ 'header': 1 }, { 'header': 2 }],               // custom button values
                    [{ 'list': 'ordered'}, { 'list': 'bullet' }],
                    [{ 'script': 'sub'}, { 'script': 'super' }],      // superscript/subscript
                    [{ 'indent': '-1'}, { 'indent': '+1' }],          // outdent/indent
                    [{ 'direction': 'rtl' }],                         // text direction

                    [{ 'size': ['small', false, 'large', 'huge'] }],  // custom dropdown
                    [{ 'header': [1, 2, 3, 4, 5, 6, false] }],

                    [{ 'color': [] }, { 'background': [] }],          // dropdown with defaults from theme
                    [{ 'font': [] }],
                    [{ 'align': [] }],                              // remove formatting button
                ]
            }
        }
    },
    created() {
        if(this.$route.params.category=='category1') this.post_category = '1'
        else if(this.$route.params.category=='category2') this.post_category = '2'
        if(this.mode=='modify' && this.$route.query.code!=undefined) {
            if(this.post_category=='1') {
                this.$https.post(`${BaseURI}/informreq`, {
                    code: this.$route.query.code
                })
                .then((response) => {
                    this.post_title = response.data.title;
                    this.post_date = response.data.date;
                    this.post_content = response.data.main;
                })
            }
            else if(this.post_category=='2') {
                this.$https.post(`${BaseURI}/informreq2`, {
                    code: this.$route.query.code
                })
                .then((response) => {
                    this.post_title = response.data.title;
                    this.post_date = response.data.date;
                    this.post_content = response.data.main;
                })
            }
        }
    },
    methods: {
        logout() {
            window.localStorage.clear();
            this.$router.push('/admin');
        },
        onEditorReady(quill) {
            console.log('editor ready!', quill)
        },
        submit() {
            if(this.post_category=='' || this.post_title=='' || this.post_content=='') {
                alert('게시판, 제목, 본문 모두 작성해주세요.');
                return ;
            }
            else {
                /* 오늘 날짜 시간 구함 */
                function pad2(n) { 
                    return n < 10 ? '0' + n : n
                }
                let date = new Date()
                let today = date.getFullYear().toString() + '.' + pad2(date.getMonth() + 1) + '.' + pad2( date.getDate()) + ' ' + pad2( date.getHours() ) + ':'+ pad2( date.getMinutes() );

                if(this.mode=='write') {
                    /* 카테고리별 작성 함수 */
                    if(this.post_category=='1') {
                        this.loading=true;
                        this.$https.post(`${BaseURI}/informw`, {
                            title: this.post_title+'/%$^#@$',
                            date: today+'/%$^#@$',
                            main: this.post_content,
                            token: window.localStorage.getItem('accessToken')
                        })
                        .then((response) => {
                            console.log(response)
                            this.loading=false;
                            alert('게시물이 게시되었습니다.');
                            this.$router.push({ path: '/edit' });
                        })
                    }
                    else if(this.post_category=='2') {
                        this.loading=true;
                        this.$https.post(`${BaseURI}/informw2`, {
                            title: this.post_title+'/%$^#@$',
                            date: today+'/%$^#@$',
                            main: this.post_content,
                            token: window.localStorage.getItem('accessToken')
                        })
                        .then((response) => {
                            console.log(response)
                            this.loading=false;
                            alert('게시물이 게시되었습니다.');
                            this.$router.push({ path: '/edit' });
                        })
                    }
                }
                else if(this.mode='modify') {
                    /* 카테고리별 수정 함수 */
                    if(this.post_category=='1') {
                        this.loading=true;
                        this.$https.post(`${BaseURI}/informmo`, {
                            code: this.$route.query.code,
                            title: this.post_title+'/%$^#@$',
                            date: this.post_date+'/%$^#@$',
                            main: this.post_content,
                            token: window.localStorage.getItem('accessToken')
                        })
                        .then((response) => {
                            console.log(response)
                            this.loading=false;
                            alert('게시물이 수정되었습니다.');
                            this.$router.push({ path: '/edit' });
                        })
                    }
                    else if(this.post_category=='2') {
                        this.loading=true;
                        this.$https.post(`${BaseURI}/informmo2`, {
                            code: this.$route.query.code,
                            title: this.post_title+'/%$^#@$',
                            date: this.post_date+'/%$^#@$',
                            main: this.post_content,
                            token: window.localStorage.getItem('accessToken')
                        })
                        .then((response) => {
                            console.log(response)
                            this.loading=false;
                            alert('게시물이 수정되었습니다.');
                            this.$router.push({ path: '/edit' });
                        })
                    }
                }
            }
        }
    }
}
</script>
<style scoped>
ul {
    padding: 0;
    list-style: none;
}
.admin_page .ad_contents_wrap {
    align-items: flex-start;
}
</style>


