<template>
    <div class="contents_wrap">
        <div class="header">
            <div class="cover">
                <div class="location_wrap">
                    <h1 class="title">
                        <span v-if="sub_title=='화학물질확인신고'">화학물질 확인신고</span>
                        <span v-else-if="sub_title=='인벤토리'">인벤토리</span>
                    </h1>
                    <div class="bar">
                        <div class="point_bar"></div>
                    </div>
                    <span class="location">확인제도 ＞
                        <span v-if="sub_title=='화학물질확인신고'">화학물질 확인신고</span>
                        <span v-else-if="sub_title=='인벤토리'">인벤토리</span>
                    </span>
                </div>
            </div>
        </div>
        <div class="contents">
            <div class="sidebar">
                <div class="sidebar-box">
                    <router-link to="/confirmsystem"><h1 class="title">확인제도</h1></router-link>
                    <div class="bar">
                        <div class="bold-bar"></div>
                    </div>
                    <div class="navbtns">
                        <router-link to="/confirmsystem/confirmsystem1" class="btn" :class="{active : sub_title=='화학물질확인신고'}">화학물질 확인신고</router-link>
                        <router-link to="/confirmsystem/confirmsystem2" class="btn" :class="{active : sub_title=='인벤토리'}">인벤토리</router-link>
                    </div>
                </div>
            </div>
            <transition name="fade" mode="out-in">
                <router-view />
            </transition>
        </div>
    </div>
</template>
<script>
export default {
    name: 'IntroducePage',
    props: ['title', 'sub_title']
}
</script>



