<template>
    <div class="contents_wrap">
        <div class="section">
            <transition-group name="slide">
                <div v-if="background == 0" :style="{'background-image': `url(${background1})`}" class="background-image" key="0"></div>
                <div v-else-if="background == 1" :style="{'background-image': `url(${background2})`}" class="background-image" key="1"></div>
                <div v-else-if="background == 2" :style="{'background-image': `url(${background3})`}" class="background-image" key="2"></div>
            </transition-group>
            <div class="text-area">
                <h1 class="title">화학물질 및 환경 규제 분야<br>전문 컨설팅 파트너, 케미파트너.</h1>
                <p class="subtxt">(주)케미파트너는 화학물질 및 환경 규제 분야 전문 컨설팅을 제공하는 기업으로<br>해당 분야 18년 이상의 경력을 통한 신속·정확한 서비스와 고객 맞춤 컨설팅을 제공합니다.</p>
            </div>
            <div class="control-row">
                <div class="control-box">
                    <span class="btn" @click="pickImage(0)" :class="[background == 0 ? 'active' : '']">
                        <span class="circle"></span>
                    </span>
                    <span class="btn" @click="pickImage(1)" :class="[background == 1 ? 'active' : '']">
                        <span class="circle"></span>
                    </span>
                    <span class="btn" @click="pickImage(2)" :class="[background == 2 ? 'active' : '']">
                        <span class="circle"></span>
                    </span>
                </div>
            </div>
        </div>
        <div class="section1">
            <div class="item" :class="{ active : active_item==0 }" @mouseover="pickItem(0)">
                <div class="icon one"></div>
                <h3 class="title">Chemipartner</h3>
                <span class="sub">화평법 공동등록에 최적화된 맞춤형 컨설팅 지원</span>
            </div>
            <div class="item" :class="{ active : active_item==1 }" @mouseover="pickItem(1)">
                <div class="icon two"></div>
                <h3 class="title">Chemipartner</h3>
                <span class="sub">고객의 눈높이에서 신속하고</br>정확하게 서비스를 제공하는  파트너</span>
            </div>
            <div class="item" :class="{ active : active_item==2 }" @mouseover="pickItem(2)">
                <div class="icon three"></div>
                <h3 class="title">Chemipartner</h3>
                <span class="sub">화학물질 및 환경규제 컨설팅 지원</span>
            </div>
        </div>
        <div class="section2">
            <div class="img-area">
                <img class="line" src="@/assets/img/main_background_shape.png" />
                <img class="person" src="@/assets/img/main_person.png" />
                <div class="cover"></div>
            </div>
            <div class="text-area">
                <div class="text-wrap">
                    <h1 class="title">당신만을 위한 컨설팅 파트너</h1>
                    <p class="sub">당신만을 위한 컨설팅 파트너, 케미파트너.당신만을 위한 컨설팅 파트너, 케미파트너.당신만을 위한 컨설팅 파트너, 케미파트너.당신만을 위한 컨설팅 파트너, 케미파트너.당신만을 위한 컨설팅 파트너, 케미파트너.당신만을 위한 컨설팅 파트너, 케미파트너.당신만을 위한 컨설팅 파트너, 케미파트너.당신만을 위한 컨설팅 파트너, 케미파트너.당신만을 위한 컨설팅 파트너, 케미파트너.당신만을 위한 컨설팅 파트너</p>
                </div>
            </div>
        </div>
        <div class="section3">
            <div class="background">
                <div class="cover">
                    <div class="wrap">
                        <div class="text-area">
                            <h1 class="title">최적의 서비스로 당신을 찾아갑니다.</h1>
                            <p class="sub">반갑습니다. 케미파트너입니다.</p>
                        </div>
                        <button class="btn">화평법 자세히보기</button>
                    </div>
                </div>
            </div>
        </div>
        <div class="section4">
            <div class="header_wrap">
                <h1 class="title">케미파트너와 소통하세요.</h1>
                <p class="sub">반갑습니다. 케미파트너입니다.</p>
            </div>
            <div class="board_wrap">
                <div class="table_card">
                    <div class="top">
                        <h1 class="title">공지사항</h1>
                        <router-link to="/community/community1"><span class="btn" style="color: #48a9d3">MORE</span></router-link>
                    </div>
                    <div class="bar">
                        <div class="thick"></div>
                    </div>
                    <div v-for="item in post_list" class="post_wrap">
                        <span class="title"><div class="title_txt" :title="item.title"><router-link :to="'community/community1/post/'+item.code">{{item.title}}</router-link></div></span>
                        <span class="date">{{item.date.slice(0,10)}}</span>
                    </div>
                </div>
                <div class="table_card">
                    <div class="top">
                        <h1 class="title">자료실</h1>
                        <router-link to="/community/community2"><span class="btn" style="color: #48a9d3">MORE</span></router-link>
                    </div>
                    <div class="bar">
                        <div class="thick"></div>
                    </div>
                    <div v-for="item in post_list_2" class="post_wrap">
                        <span class="title"><div class="title_txt"><router-link :to="'community/community2/post/'+item.code">{{item.title}}</router-link></div></span>
                        <span class="date">{{item.date.slice(0,10)}}</span>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import background1 from '@/assets/img/main_background_1.png';
import background2 from '@/assets/img/main_background_2.png';
import background3 from '@/assets/img/main_background_3.png';

const BaseURI = 'http://www.chemipartner.com'

export default {
    name: 'MainPage',
    data() {
        return {
            background: 0,
            background1,
            background2,
            background3,
            slideTimer: undefined,
            itemTimer: undefined,
            active_item: 0,

            post_list: [],
            post_list_2: []
        }
    },
    created() {
        this.$https.post(`${BaseURI}/inform`, {
            limit: 6,
            pagenum: 1
        })
        .then((response) => {
            this.post_list = response.data.list
        })
        this.$https.post(`${BaseURI}/inform2`, {
            limit: 6,
            pagenum: 1
        })
        .then((response) => {
            this.post_list_2 = response.data.list
        })
    },
    mounted() {
        this.slideTimer = setInterval(() => {
            this.background++;
            if(this.background == 3) this.background = 0;
        }, 6000);
        this.itemTimer = setInterval(() => {
            this.active_item++;
            if(this.active_item == 3) this.active_item = 0;
        }, 5000);
    },
    methods: {
        pickImage(num) {
            this.background = num;
            clearInterval(this.slideTimer);
            this.slideTimer = setInterval(() => {
                this.background++;
                if(this.background == 3) this.background = 0;
            }, 6000);
        },
        pickItem(num) {
            this.active_item = num;
            clearInterval(this.itemTimer);
            this.itemTimer = setInterval(() => {
                this.active_item++;
                if(this.active_item == 3) this.active_item = 0;
            }, 5000);
        }
    }
}
</script>



