<template>
    <div class="contents-box">
        <div class="title-wrap">
            <div class="bar"></div>
            <h1 class="title">신규화학물질 유해성ㆍ위험성 조사</h1>
        </div>
        <div class="item-wrap">
            <p class="text" style="margin-bottom: 20px;">신규화학물질을 제조ㆍ수입하려는 자는 제조 또는 수입 전에 그 신규화학물질의 유해성ㆍ위험성을 조사하고 그 조사보고서를 고용노동부장관에게 제출하여야 합니다.</p>
            <p class="text">다만, 화평법에 따라 등록한 신규화학물질은 유해성ㆍ위험성 조사보고서를 제출한 것으로 봅니다.<br>(100킬로그램 이상 1톤 미만의 신규화학물질은 2019년 12월 31일까지는 제출 필요)</p>
            <div class="img-row">
                <img class="img12" src="@/assets/img/chemilaw_img15.png" />
            </div>
            <div class="item-box" style="margin-top: 20px;">
                <div class="row">
                    <List 
                        title="신규화학물질 유해성ㆍ위험성 조사 제외"
                        :contents="[
                            '일반소비자 생활용',
                            '소량(100킬로그램 미만)',
                            '시험ㆍ연구용',
                            '연간 10톤 이하 전량 수출용',
                            '저우려고분자화합물'
                        ]"
                    />
                </div>
                <div class="row">
                    <List 
                        title="제공 서비스"
                        :contents="[
                            '유해성ㆍ위험성 조사 대상 여부 판단',
                            '유해성ㆍ위험성 조사 보고서 작성 대행'
                        ]"
                    />
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import List from './StyledList';
export default {
    name: 'MSDS1',
    components: {
        List
    }
}
</script>



