<template>
    <div class="admin_page">
        <div class="top_bar">
            <router-link to="/"><img src="@/assets/img/logo_img_h_w.png" /></router-link>
            <span class="btn" @click="logout">LOGOUT</span>
        </div>
        <div class="ad_contents_wrap">
            <div class="ad_board_wrap">
                <div class="col">
                    <div class="title_area">
                        <span class="title">공지사항</span>
                        <router-link to="/posting/write"><span class="btn">게시물 작성</span></router-link>
                    </div>
                    <div class="board_area">
                        <table>
                            <tr>
                                <th style="width: 9%;">번호</th>
                                <th sylte="width: 55%">게시물</th>
                                <th style="width: 18%;">작성일자</th>
                                <th style="width: 18%;">수정 및 삭제</th>
                            </tr>
                            <tr v-for="(item,index) in post_list_1">
                                <td>{{(pageNum_1-1)*post_limit+index+1}}</td>
                                <td><div class="title-td" :title="item.title"><router-link target="" :to="'community/community1/post/'+item.code">{{item.title}}</router-link></div></td>
                                <td class="date">{{item.date.slice(0,10)}}</td>
                                <td><router-link :to="'posting/modify/category1?code='+item.code"><span class="btn">수정</span></router-link><span class="btn" @click="del_post_1(item.code)">삭제</span></td>
                            </tr>
                        </table>
                        <div class="paging">
                            <paginate
                            :page-count="max_page_1"
                            :force-page="pageNum_1"
                            :click-handler="page_change_1"
                            :prev-text="'Prev'"
                            :next-text="'Next'"
                            :container-class="'pagination'">
                            </paginate>
                        </div>
                    </div>
                </div>
                <div class="col">
                    <div class="title_area">
                        <span class="title">자료실</span>
                        <router-link to="/posting/write"><span class="btn">게시물 작성</span></router-link>
                    </div>
                    <div class="board_area">
                        <table>
                            <tr>
                                <th style="width: 9%;">번호</th>
                                <th sylte="width: 55%">게시물</th>
                                <th style="width: 18%;">작성일자</th>
                                <th style="width: 18%;">수정 및 삭제</th>
                            </tr>
                            <tr v-for="(item,index) in post_list_2">
                                <td>{{(pageNum_2-1)*post_limit+index+1}}</td>
                                <td><div class="title-td" :title="item.title"><router-link target="" :to="'community/community2/post/'+item.code">{{item.title}}</router-link></div></td>
                                <td class="date">{{item.date.slice(0,10)}}</td>
                                <td><router-link :to="'posting/modify/category2?code='+item.code"><span class="btn">수정</span></router-link><span class="btn" @click="del_post_2(item.code)">삭제</span></td>
                            </tr>
                        </table>
                        <div class="paging">
                            <paginate
                            :page-count="max_page_2"
                            :force-page="pageNum_2"
                            :click-handler="page_change_2"
                            :prev-text="'Prev'"
                            :next-text="'Next'"
                            :container-class="'pagination'">
                            </paginate>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        
    </div>
</template>
<script>
const BaseURI = 'http://www.chemipartner.com'

export default {
    name: 'EditPage',
    data() {
        return {
            post_limit: 8,

            post_list_1: [],
            max_page_1: 0,
            pageNum_1: 1,

            post_list_2: [],
            max_page_2: 0,
            pageNum_2: 1,
        }
    },
    created() {
        this.$https.post(`${BaseURI}/inform`, {
            limit: this.post_limit,
            pagenum: this.pageNum_1
        })
        .then((response) => {
            this.max_page_1 = Math.floor(response.data.totalpage)
            this.post_list_1 = response.data.list
        })
        this.$https.post(`${BaseURI}/inform2`, {
            limit: this.post_limit,
            pagenum: this.pageNum_2
        })
        .then((response) => {
            this.max_page_2 = Math.floor(response.data.totalpage)
            this.post_list_2 = response.data.list
        })
    },
    methods: {
        logout() {
            window.localStorage.clear()
            this.$router.push('/admin')
        },
        page_change_1(pageNum) {
            this.pageNum_1 = pageNum;
            this.$https.post(`${BaseURI}/inform`, {
                limit: this.post_limit,
                pagenum: this.pageNum_1
            })
            .then((response) => {
                this.max_page_1 = Math.floor(response.data.totalpage)
                this.post_list_1 = response.data.list
            })
        },
        del_post_1(code) {
            this.$https.post(`${BaseURI}/informdl`, {
                code: code
            })
            .then((response) => {
                console.log('Delete : ' + code);
                alert('게시물이 삭제 되었습니다.')
            })
        },
        page_change_2(pageNum) {
            this.pageNum_2 = pageNum
            this.$https.post(`${BaseURI}/inform2`, {
                limit: this.post_limit,
                pagenum: this.pageNum_2
            })
            .then((response) => {
                this.max_page_2 = Math.floor(response.data.totalpage)
                this.post_list_2 = response.data.list
            })
        },
        del_post_2(code) {
            this.$https.post(`${BaseURI}/informdl2`, {
                code: code
            })
            .then((response) => {
                console.log('Delete : ' + code);
                alert('게시물이 삭제 되었습니다.')
            })
        },
    }
}
</script>
<style scoped>
ul {
    padding: 0;
    list-style: none;
}
.admin_page .ad_contents_wrap {
    align-items: flex-start;
}
</style>


