        </div>
        <div class="item-wrap">
            <p class="text">화평법에서 정의된 중간체의 경우, 해당 조건에 따라 화학물질의 등록 시 특례가 적용되어 제조 · 수입량 톤수 범위별 제출 시험자료가 간소화되거나, 면제 조건에 부합하는 경우에는 등록 또는 신고가 아닌 등록 · 신고 면제를 받을 수 있습니다. 사업장에서 제조 또는 수입하는 화학물질이 중간체로 판단되는 경우에는 해당 중간체가 화평법에 따른 정의나 면제 조건 등에 부합하는 지를 확인하여야 합니다.</p>
            <div class="img-row">
