                        :contents="[
                            '화학물질의 제조, 정제, 이송, 시료채취, 분석, 세정 및 장비 보수, 장비 또는 용기의 선적 및 하적, 폐기물의 처리 또는 정화 및 창고 보관을 포함한 해당 화학물질의 전체 주기 동안 화학물질의 유출 및 모든 인체 · 환경으로의 노출을 최소화하기 위한 통제기술 및 관리절차가 사용되어 엄격하게 통제 · 관리될 것',
                            '훈련을 받은 담당자만이 해당 화학물질을 취급할 것',
                            '세정 및 유지관리 작업의 경우, 제조공정이 시작되어 해당 화학물질이 투입되기 전에 정화 및 세척 등의 별도의 절차가 있을 것',
                            '사고가 발생하여 해당 화학물질에 대한 폐기물이 생성되는 경우, 정화, 청소 및 유지보수 과정에서 발생하는 화학물질의 유출 및 모든 인체 · 환경으로의 노출을 최소화하기 위한 통제기술 및 관리절차가 사용될 것',
