<template>
    <div class="contents-box">
        <div class="title-wrap">
            <div class="bar"></div>
            <h1 class="title">고분자 화합물</h1>
        </div>
        <div class="item-wrap">
            <p class="text">화평법에서 정의된 고분자화합물의 경우, 화학물질의 등록 시 특례가 적용되어 제조 · 수입량 톤수 범위별 제출 시험자료가 간소화되거나, 면제 조건에 부합하는 경우에는 등록 또는 신고가 아닌 등록 · 신고 면제를 받을 수 있습니다. 사업장에서 화학물질이 고분자화합물로 판단되는 경우에는 해당 고분자화합물이 화평법에 따른 정의나 면제 조건 등에 부합하는지를 확인하여야 합니다.</p>
            <div class="item-box" style="margin-top: 35px;">
                <div class="row">
                    <List 
                        title="화평법에 따른 고분자 화합물의 정의"
                        explanation="다음 요건을 모두 갖춘 화학물질 중량비 2퍼센트 이하의 단량체를 제외한 단량체로 구성된 고분자화합물이 기존화학물질에 해당하는 경우 그 고분자화합물은 기존화학물질로 본다."
                        :contents="[
                            '1종 이상의 단량체단위가 연속하여 반복되는 분자로 이루어져 있을 것',
                            '각 분자 내 단량체단위의 반복수(反復數)에 따라 특징적 분자량 분포를 보일 것',
                            '세 개 이상의 단량체단위가 적어도 한 개 이상의 단량체단위 또는 다른 반응물과 공유결합을 이루는 분자가 50퍼센트 이상일 것',
                            '분자량이 같은 분자가 중량비로 50퍼센트를 초과하지 아니할 것'
                        ]"
                    />
                </div>
            </div>
            <div class="img-row">
                <img class="img8" src="@/assets/img/chemilaw_img8.png" />
            </div>
            <div class="item-box">
                <div class="row">
                    <List 
                        title="제공 서비스" 
                        :contents="[
                            '법령에 따른 고분자화합물 여부 확인',
                            '면제 적용 여부 확인',
                            '고분자화합물 등록ㆍ신고 면제',
                            '고분자화합물 등록 · 신고',
                        ]"
                    />
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import List from './StyledList';
export default {
    name: 'Chemistrylaw6',
    components: {
        List
    }
}
</script>



