<template>
    <div class="admin_page">
        <div class="top_bar">
            <router-link to="/"><img src="@/assets/img/logo_img_h_w.png" /></router-link>
        </div>
        <div class="ad_contents_wrap">
            <div class="contents_card">
                <div class="header">
                    <div class="text_wrap">
                        <h1 class="title">ADMINISTRATOR</h1>
                        <p class="sub">(주)케미파트너의 관리자 페이지 입니다.</p>
                    </div>
                </div>
                <div class="input_wrap">
                    <input v-model="id" class="input" type="text" placeholder="아이디를 입력해주세요." />
                    <input v-model="pass" class="input" type="password" placeholder="비밀번호를 입력해주세요." />
                    <button class="submit_btn" @click="submit">LOGIN</button>
                </div>
                <div class="bottom">
                    <router-link to="/">CHEMIPARTNER 페이지로 이동하기</router-link>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
const BaseURI = 'http://www.chemipartner.com'
export default {
    name: 'AdminPage',
    data() {
        return {
            id: 'admin',
            pass: 'chemipartner2019!',
        }
    },
    methods: {
        submit() {
            this.$https.post(`${BaseURI}/adminout?id=${this.id}&pass=${this.pass}`)
            .then((response) => {
                if(response.data === '1') alert('아이디 또는 비밀번호가 잘못되었습니다.');
                else {
                    window.localStorage.setItem('accessToken', response.data);
                    this.$router.push('/edit');
                }
            })
        }
    }
}
</script>
<style scoped>
</style>



