<template>
    <div class="contents-box">
        <div class="title-wrap">
            <div class="bar"></div>
            <h1 class="title">기존화학물질 사전신고</h1>
        </div>
        <div class="item-wrap">
            <p class="text">연간 1톤 이상 기존화학물질을 제조ㆍ수입하려는 자는 제조 또는 수입 전에 등록하여야 하지만 등록유예기간 동안에는 등록을 하지 아니하고 제조ㆍ수입할 수 있습니다. 등록유예기간 동안 등록을 하지 아니하고 제조ㆍ수입하려는 자는 제조 또는 수입 전에 신고하여야 합니다.</p>
            <div class="img-row">
                <img class="img2" src="@/assets/img/chemilaw_img2.png" />
                <img class="img3" src="@/assets/img/chemilaw_img3.png" />
            </div>
            <div class="item-box">
                <div class="row">
                    <List 
                        title="제공 서비스" 
                        :contents="[
                            '(늦은) 사전신고 대상 여부 판단',
                            '(늦은) 사전신고 대행',
                            '유일대리인(OR)',
                        ]"
                    />
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import List from './StyledList';
export default {
    name: 'Chemistrylaw2',
    components: {
        List
    }
}
</script>



