<template>
    <div class="contents-box">
        <div v-if="page_title=='커뮤니티'" class="post-wrap">
            <div class="title-wrap">
                <div class="bar"></div>
                <h1 class="title">자료실</h1>
            </div>
        </div>
        <router-view />
        <div class="post-list-wrap">
            <table class="table">
                <tr>
                    <th>게시물</th>
                    <th style="width: 18%">작성일자</th>
                    <th style="width: 14%">작성자</th>
                </tr>
                <tr v-for="item in post_list">
                    <td><div class="title-td"><router-link :to="`/community/community2/post/${item.code.split('.')[0]}`"><span style="color: #002741">{{item.title}}</span></router-link></div></td>
                    <td>{{item.date.slice(0,10)}}</td>
                    <td>관리자</td>
                </tr>
            </table>
            <paginate
            :page-count="max_page"
            :force-page="pageNum"
            :click-handler="page_change"
            :prev-text="'Prev'"
            :next-text="'Next'"
            :container-class="'pagination'">
            </paginate>
        </div>
    </div>
</template>
<script>

const BaseURI = 'http://www.chemipartner.com'

export default {
    name: 'Community2',
    data() {
        return {
            page_title: '',
            post_limit: 10,
            post_list: [],
            max_page: 10,
            pageNum: 1,
        }
    },
    created() {
        this.page_title = this.$route.matched[this.$route.matched.length-1].meta.title;
        this.$https.post(`${BaseURI}/inform2`, {
            limit: this.post_limit,
            pagenum: this.pageNum
        })
        .then((response) => {
            this.max_page = Math.floor(response.data.totalpage);
            this.post_list = response.data.list
        })
    },
    methods: {
        page_change(pageNum) {
            this.pageNum = pageNum;
            this.$https.post(`${BaseURI}/inform2`, {
                limit: 9,
                pagenum: this.pageNum
            })
            .then((response) => {
                this.max_page = Math.floor(response.data.totalpage);
                this.post_list = response.data.list
            })
        }
    },
    watch: {
        '$route' (to, from) {
            this.page_title = this.$route.matched[this.$route.matched.length-1].meta.title;
        }
    }
}
</script>
<style scoped>
@media (min-width : 1024px) {
    .contents_wrap .contents .contents-box {
        padding-right: 100px;
    }
}
</style>


