<template>
    <div class="item">
        <div class="title-area">
            <div class="bar"></div>
            <h1 class="title">{{title}}</h1>
        </div>
        <p class="explain-text">{{explanation}}</p>
        <ol v-if="type=='num'">
            <li v-for="(item, index) in contents">
                <span class="txt">
                    {{item}}
                </span>
                <div class="sub-wrap" v-if="contents_sub[index].length">
                    <span v-for="sub in contents_sub[index]" class="sub">{{sub}}</span>
                </div>
            </li>
        </ol>
        <ul v-else>
            <li v-for="(item, index) in contents">
                <span class="txt">
                    {{item}}
                </span>
                <div class="sub-wrap" v-if="contents_sub[index].length">
                    <span v-for="sub in contents_sub[index]" class="sub">{{sub}}</span>
                </div>
            </li>
        </ul>
    </div>
</template>
<script>
export default {
    name: 'StyledList',
    props: {
        'title': {},
        'explanation': {},
        'contents': {},
        'contents_sub': {
            default: [[],[],[],[],[],[],[],[]]
        },
        'type': {}
    }
}
</script>
<style scoped>
.contents_wrap .contents .contents-box .item-wrap .item-box .row .item .title-area {
    display: flex;
    margin-bottom: 20px;
    height: auto;
}
.contents_wrap .contents .contents-box .item-wrap .item-box .row .item .title-area .bar {
    min-width: 5px;
    background-color: #48a9d3;
    margin-right: 10px;
}
.contents_wrap .contents .contents-box .item-wrap .item-box .row .item .title-area .title {
    font-family: 'AppleSDGothicNeoEB'!important;
    font-weight: 600;
    font-size: 24px;
    margin: 0;
}
.contents_wrap .contents .contents-box .item-wrap .item-box .row .item .explain-text {
    margin: 20px 0;
    line-height: 160%;
    font-size: 16px;
}
.contents_wrap .contents .contents-box .item-wrap .item-box .row .item ul, .contents_wrap .contents .contents-box .item-wrap .item-box .row .item ol {
    padding-left: 20px;
}
.contents_wrap .contents .contents-box .item-wrap .item-box .row .item ol li {
    margin-bottom: 4px;
    font-size: 16px;
    color: #48a9d3;
    font-weight: 600;
    line-height: 190%;
}
.contents_wrap .contents .contents-box .item-wrap .item-box .row .item ul li {
    margin-bottom: 18px;
    font-size: 16px;
    color: #333;
}
.contents_wrap .contents .contents-box .item-wrap .item-box .row .item li .txt {
    color: #333;
    font-weight: 400;
}
.contents_wrap .contents .contents-box .item-wrap .item-box .row .item li .sub-wrap {
    margin-top: 12px;
}
.contents_wrap .contents .contents-box .item-wrap .item-box .row .item li .sub-wrap .sub {
    display: block;
    margin-bottom: 12px;
}
@media (max-width : 1023px) {
    .contents_wrap .contents .contents-box .item-wrap .item-box .row .item ul li {
        font-size: 16px;
        line-height: 180%;
        margin-bottom: 5px;
    }
    .contents_wrap .contents .contents-box .item-wrap .item-box .row .item .title-area .title {
        font-size: 24px;
        line-height: 140%;
    }
    .contents_wrap .contents .contents-box .item-wrap .item-box .row .item .explain-text {
        font-size: 16px;
        line-height: 180%;
    }
}
</style>


