<template>
    <div class="contents-box">
        <div class="title-wrap">
            <div class="bar"></div>
            <h1 @click="submit" class="title">오시는 길</h1>
        </div>
        <div class="item-wrap">
            <div id="map" class="map"></div>
            <div class="info-area">
                <div class="card">
                    <h1 class="card-title">주소 및 연락처</h1>
                    <div class="info-wrap">
                        <div class="title">
                            <img class="icon" src="@/assets/img/icon_building.png"/>
                            <h1>ADDRESS</h1>
                        </div>
                        <p class="info">서울특별시 구로구 디지털로30길 31 코오롱디지털타워빌란트2차 1405호</p>
                    </div>
                    <div class="info-wrap">
                        <div class="title">
                            <img class="icon" src="@/assets/img/icon_call.png"/>
                            <h1>TEL</h1>
                        </div>
                        <p class="info">02-6964-7119</p>
                    </div>
                    <div class="info-wrap">
                        <div class="title">
                            <img class="icon" src="@/assets/img/icon_print.png"/>
                            <h1>FAX</h1>
                        </div>
                        <p class="info">02-867-7079</p>
                    </div>
                    <div class="info-wrap">
                        <div class="title">
                            <img class="icon" src="@/assets/img/icon_email.png"/>
                            <h1>E-mail</h1>
                        </div>
                        <p class="info">eksong@chemipartner.com</p>
                    </div>
                </div>
                <div class="card">
                    <h1 class="card-title">대중교통 이용정보</h1>
                    <div class="info-wrap">
                        <div class="title">
                            <img class="icon" src="@/assets/img/icon_subway.png"/>
                            <h1>SUBWAY</h1>
                        </div>
                        <p class="info"> 2호선 구로디지털단지역 2, 3번 출구</p>
                    </div>
                    <div class="info-wrap">
                        <div class="title">
                            <img class="icon" src="@/assets/img/icon_bus.png"/>
                            <h1>BUS</h1>
                        </div>
                        <p class="info">JNK디지털타워 – 금천07<br>한국산업단지공단, 이마트구로점 – 5536, 5616, 구로09, 영등포</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
const BaseURI = 'http://www.chemipartner.com'

export default {
    name: 'IntroLocation',
    data() {
        return {
            Lat: 37.483098,
            Lng: 126.896091
        }
    },
    mounted() {
        var container = document.getElementById('map'); //지도를 담을 영역의 DOM 레퍼런스
        var options = { //지도를 생성할 때 필요한 기본 옵션
            center: new kakao.maps.LatLng(this.Lat, this.Lng), //지도의 중심좌표.
            level: 3 //지도의 레벨(확대, 축소 정도)
        };

        var map = new kakao.maps.Map(container, options); //지도 생성 및 객체 리턴
        var markerPosition  = new kakao.maps.LatLng(this.Lat, this.Lng); 

        // 마커를 생성합니다
        var marker = new kakao.maps.Marker({
            position: markerPosition
        });
        marker.setMap(map);
    },
    methods: {
        submit() {
            this.$https.post(`${BaseURI}/informw`, {
                title: '섹스할래요.',
                date: '2020-01-01',
                main: '<h1>Apollo 11<img alt="" src="https://ckeditor.com/apps/ckfinder/userfiles/images/chemilaw_img12.png" style="float:right; height:133px; width:100px" /></h1><p><strong>Apollo 11</strong> was the spaceflight that landed the first humans, Americans <a href="http://en.wikipedia.org/wiki/Neil_Armstrong">Neil Armstrong</a> and <a href="http://en.wikipedia.org/wiki/Buzz_Aldrin">Buzz Aldrin</a>, on the Moon on July 20, 1969, at 20:18 UTC. Armstrong became the first to step onto the lunar surface 6 hours later on July 21 at 02:56 UTC.</p><p>Armstrong spent about <s>three and a half</s> two and a half hours outside the spacecraft, Aldrin slightly less; and together they collected 47.5 pounds (21.5&nbsp;kg) of lunar material for return to Earth. A third member of the mission, <a href="http://en.wikipedia.org/wiki/Michael_Collins_(astronaut)">Michael Collins</a>, piloted the <a href="http://en.wikipedia.org/wiki/Apollo_Command/Service_Module">command</a> spacecraft alone in lunar orbit until Armstrong and Aldrin returned to it for the trip back to Earth.</p>',
                token: window.localStorage.getItem('accessToken')
            })
            .then((response) => {
                console.log(response)
            })
        }
    }
}
</script>
<style scoped>
    .item-wrap {
        display: flex;
    }
    @media (max-width : 1023px) {
        .item-wrap {
            flex-direction: column
        }
        .map {
            flex: none!important;
            width: 100%;
            height: 300px;
            margin: 0;
            margin-top: 4px;
        }
        .contents_wrap .contents .contents-box .item-wrap .info-area .card .info-wrap .title h1 {
            font-size: 23px;
        }
        .contents_wrap .contents .contents-box .item-wrap .info-area .card .info-wrap .title .icon {
            width: 22px;
            height: 22px;
        }
        .contents_wrap .contents .contents-box .item-wrap .info-area .card .info-wrap .info {
            font-size: 15px;
        }
    }
</style>



