<template>
    <div class="contents_wrap">
        <div class="header">
            <div class="cover">
                <div class="location_wrap">
                    <h1 class="title">
                        <span v-if="sub_title=='회사소개'">회사소개</span>
                        <span v-else-if="sub_title=='오시는길'">오시는 길</span>
                    </h1>
                    <div class="bar">
                        <div class="point_bar"></div>
                    </div>
                    <span class="location">회사소개 ＞
                        <span v-if="sub_title=='회사소개'">회사소개</span>
                        <span v-else-if="sub_title=='오시는길'">오시는 길</span>
                    </span>
                </div>
            </div>
        </div>
        <div class="contents">
            <div class="sidebar">
                <div class="sidebar-box">
                    <router-link to="/introduce"><h1 class="title">회사소개</h1></router-link>
                    <div class="bar">
                        <div class="bold-bar"></div>
                    </div>
                    <div class="navbtns">
                        <router-link to="/introduce/intro" class="btn" :class="{active : sub_title=='회사소개'}">회사소개</router-link>
                        <router-link to="/introduce/location" class="btn" :class="{active : sub_title=='오시는길'}">오시는 길</router-link>
                    </div>
                </div>
            </div>
            <transition name="fade" mode="out-in">
                <router-view />
            </transition>
        </div>
    </div>
</template>
<script>
export default {
    name: 'IntroducePage',
    props: ['title', 'sub_title'],
}
</script>



