<template>
    <div class="contents_wrap">
        <div class="header">
            <div class="cover">
                <div class="location_wrap">
                    <h1 class="title">
                        <span v-if="sub_title=='공지사항'">공지사항</span>
                        <span v-else-if="sub_title=='자료실'">자료실</span>
                    </h1>
                    <div class="bar">
                        <div class="point_bar"></div>
                    </div>
                    <span class="location">커뮤니티 ＞
                        <span v-if="sub_title=='공지사항'">공지사항</span>
                        <span v-else-if="sub_title=='자료실'">자료실</span>
                    </span>
                </div>
            </div>
        </div>
        <div class="contents">
            <div class="sidebar">
                <div class="sidebar-box">
                    <router-link to="/community"><h1 class="title">커뮤니티</h1></router-link>
                    <div class="bar">
                        <div class="bold-bar"></div>
                    </div>
                    <div class="navbtns">
                        <router-link to="/community/community1" class="btn" :class="{active : sub_title=='공지사항'}">공지사항</router-link>
                        <router-link to="/community/community2" class="btn" :class="{active : sub_title=='자료실'}">자료실</router-link>
                    </div>
                </div>
            </div>
            <transition name="fade" mode="out-in">
                <router-view />
            </transition>
        </div>
    </div>
</template>
<script>
export default {
    name: 'CommunityPage',
    props: ['title', 'sub_title']
}
</script>



