<template>
    <div class="contents-box">
        <div class="title-wrap">
            <div class="bar"></div>
            <h1 class="title">물질안전보건자료(MSDS)의 일부 비공개 승인</h1>
        </div>
        <div class="item-wrap">
            <p class="text">영업비밀과 관련되어 화학물질의 명칭 및 함유량을 MSDS에 적지 아니하려는 자는 고용노동부 장관의 승인을 받아 대체자료로 적을 수 있습니다.</p>
            <div class="img-row">
                <img class="img13" src="@/assets/img/chemilaw_img13.png" />
                <img class="img14" src="@/assets/img/chemilaw_img14.png" />
            </div>
            <div class="item-box" style="margin-top: 25px;">
                <div class="row">
                    <List 
                        title="제공 서비스"
                        :contents="[
                            '총칭명 작성 컨설팅',
                            '비공개 승인 (영업비밀 승인) 신청'
                        ]"
                    />
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import List from './StyledList';
export default {
    name: 'MSDS2',
    components: {
        List
    }
}
</script>



