<template>
    <div class="contents-box">
        <div class="title-wrap" style="margin-bottom: 0;">
            <div class="bar"></div>
            <h1 class="title" >기타 서비스</h1>
        </div>
        <div class="item-wrap">
            <div class="item-box" style="margin-top: 25px;">
                <div class="row">
                    <List 
                        :contents="[
                            '화학물질(Chemicals) 규제대응에 관한 전문지식과 경험으로 국내외 화학물질 규제 대응 컨설팅 제공',
                            '급변하는 화학물질 관련 규제에 빠르게 대응하여 국내외 시장에서의 경쟁력 강화'
                        ]"
                    />
                </div>
            </div>
            <div class="img-row">
                <img class="img16" src="@/assets/img/chemilaw_img16.png" />
            </div>
        </div>
    </div>
</template>
<script>
import List from './StyledList';
export default {
    name: 'MSDS1',
    components: {
        List
    }
}
</script>



