<template>
    <div class="contents-box">
        <div class="title-wrap">
            <div class="bar"></div>
            <h1 class="title">화학물질 확인신고</h1>
        </div>
        <p class="text">화학물질관리법에 따라 화학물질을 제조하거나 수입하려는 자는 해당 화학물질이나 그 성분 정보를 확인하여 환경부장관에게 신고하여야 합니다.(화관법 개정안)</p>
        <div class="item-wrap">
            <div class="img-row">
                <img class="img10" src="@/assets/img/chemilaw_img10.png" />
                <img class="img11" src="@/assets/img/chemilaw_img11.png" />
            </div>
            <div class="item-box" style="margin-top: 25px;">
                <div class="row">
                    <List 
                        title="제공 서비스"
                        :contents="[
                            '신고 대상 여부 판단',
                            '화학물질 확인신고',
                            '유일대리인(OR)'
                        ]"
                    />
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import List from './StyledList';
export default {
    name: 'Chemistrylaw6',
    components: {
        List
    }
}
</script>



