<template>
    <div class="contents-box">
        <div class="title-wrap">
            <div class="bar"></div>
            <h1 class="title">화평법</h1>
        </div>
        <div class="item-wrap">
            <p class="text">화학물질의 등록 및 평가 등에 관한 법률(이하, 화평법)은 기업이 유통 전에 화학물질 정보를 등록하고, 이를 토대로 정부는 유해성 심사·위해성 평가 및 유해화학물질을 지정·관리함으로써 국민건강 및 환경을 보호하는 것을 목적으로 합니다.</p>
            <p class="text bold">2019년 1월 1일부터 시행된 개정 화평법에 따라 유해성 정보 확보가 확대되어, 연간 100킬로그램 이상 신규화학물질 또는 연간 1톤 이상 기존화학물질을 제조·수입하려는 자는 제조 또는 수입 전에 등록하여야 합니다.</p>
            <img class="img1" src="@/assets/img/chemilaw_img1.png" />
            <div class="item-box">
                <div class="row">
                    <List 
                        title="제공 서비스" 
                        :contents="[
                            '기존화학물질 등록 및 협의체 운영 관리',
                            '신규화학물질 등록',
                            '유일대리인(OR)',
                            '데이터 공유 및 협상',
                            '위해성평가 보고서 작성',
                            '시험의뢰 대행 및 모니터링'
                        ]"
                    />
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import List from './StyledList';

export default {
    name: 'Chemistrylaw1',
    components: {
        List
    }
}
</script>



