<template>
    <div class="post-wrap">
        <div class="title-wrap">
            <div class="bar"></div>
            <h1 class="title">{{post_title}}</h1>
        </div>
        <div class="info-wrap">
            <span>관리자</span>
            <span>{{post_date}}</span>
        </div>
        <div class="post-contents">
            <div class="ql-snow">
                <div class="ql-editor">
                    <span v-html="post_contents"></span>
                </div>
            </div>
        </div>
        <div class="post-bottom">
            <router-link to="/community/community2"><span class="btn">목록</span></router-link>
        </div>
    </div>
</template>
<script>
const BaseURI = 'http://www.chemipartner.com'

export default {
    name: 'Community2_2',
    data() {
        return {
            post_title: '',
            post_date: '',
            post_contents: '',
        }
    },
    created() {
        this.$https.post(`${BaseURI}/informreq2`, {
            code: this.$route.params.id
        })
        .then((response) => {
            this.post_title = response.data.title;
            this.post_date = response.data.date;
            this.post_contents = response.data.main;
        })
    },
    watch: {
        '$route' (to, from) {
            this.$https.post(`${BaseURI}/informreq2`, {
                code: this.$route.params.id
            })
            .then((response) => {
                this.post_title = response.data.title;
                this.post_date = response.data.date;
                this.post_contents = response.data.main;
            })
        }
    }
}
</script>
<style scoped>
.contents_wrap .contents .contents-box {
    padding-right: 100px;
}
</style>


