<template>
  <div ref="app" id="app" v-on:scroll.passive="onScroll">
    <div class="navbar" v-if="page_title != 'admin'" :class="{background : navbackground || mobile_menu_opened, opened : mobile_menu_opened}">
      <router-link to="/"><img class="logo" src="@/assets/img/logo_img_h_w.png" /></router-link>
      <div class="menu" :class="{opened: mobile_menu_opened}">
        <div class="btn-wrap" :class="{opened: dropdown_num==0}" @mouseover="dropdown_num=0" @mouseleave="dropdown_num=-1">
          <span class="name" :class="{active : page_title=='회사소개'}">회사 소개</span>
          <img src="@/assets/img/icon_down_arrow.png" />
          <transition name="dropdown">
            <div v-if="dropdown_num==0" class="dropdown">
              <span @click="mobile_menu_opened=false"><router-link to="/introduce/intro" class="btn" :class="{active : page_sub=='회사소개'}">회사 소개</router-link></span>
              <span @click="mobile_menu_opened=false"><router-link to="/introduce/location" class="btn" :class="{active : page_sub=='오시는길'}">오시는 길</router-link></span>
            </div>
          </transition>
        </div>
        <div class="btn-wrap" :class="{opened: dropdown_num==1}" @mouseover="dropdown_num=1" @mouseleave="dropdown_num=-1">
          <span class="name" :class="{active : page_title=='화평법'}">화평법</span>
          <img src="@/assets/img/icon_down_arrow.png" />
          <transition name="dropdown">
            <div class="dropdown" v-if="dropdown_num==1">
              <span @click="mobile_menu_opened=false"><router-link to="/chemistrylaw/chemistrylaw" class="btn" :class="{active : page_sub=='화평법'}">화평법</router-link></span>
              <span @click="mobile_menu_opened=false"><router-link to="/chemistrylaw/beforedeclaration" class="btn" :class="{active : page_sub=='기존화학물질사전신고'}">기존 화학물질 사전신고</router-link></span>
              <span @click="mobile_menu_opened=false"><router-link to="/chemistrylaw/beforesignup" class="btn" :class="{active : page_sub=='기존화학물질등록'}">기존 화학물질 등록</router-link></span>
              <span @click="mobile_menu_opened=false"><router-link to="/chemistrylaw/newchemi" class="btn" :class="{active : page_sub=='신규화학물질신고등록'}">신규 화학물질 신고 · 등록</router-link></span>
              <span @click="mobile_menu_opened=false"><router-link to="/chemistrylaw/exemption" class="btn" :class="{active : page_sub=='신고등록면제'}">신고 · 등록 면제</router-link></span>
              <span @click="mobile_menu_opened=false"><router-link to="/chemistrylaw/polymercompound" class="btn" :class="{active : page_sub=='고분자화합물'}">고분자화합물</router-link></span>
              <span @click="mobile_menu_opened=false"><router-link to="/chemistrylaw/intermediate" class="btn" :class="{active : page_sub=='중간체'}">중간체</router-link></span>
            </div>
          </transition>
        </div>
        <div class="btn-wrap" :class="{opened: dropdown_num==2}" @mouseover="dropdown_num=2" @mouseleave="dropdown_num=-1">
          <span class="name" :class="{active : page_title=='확인제도'}">확인제도</span>
          <img src="@/assets/img/icon_down_arrow.png" />
          <transition name="dropdown">
            <div v-if="dropdown_num==2" class="dropdown">
              <span @click="mobile_menu_opened=false"><router-link to="/confirmsystem/confirmsystem1" class="btn" :class="{active : page_sub=='화학물질확인신고'}">화학물질 확인신고</router-link></span>
              <span @click="mobile_menu_opened=false"><router-link to="/confirmsystem/confirmsystem2" class="btn" :class="{active : page_sub=='인벤토리'}">인벤토리</router-link></span>
            </div>
          </transition>
        </div>
        <div class="btn-wrap" :class="{opened: dropdown_num==3}" @mouseover="dropdown_num=3" @mouseleave="dropdown_num=-1">
          <span class="name" :class="{active : page_title=='MSDS'}">MSDS</span>
          <img src="@/assets/img/icon_down_arrow.png" />
          <transition name="dropdown">
            <div v-if="dropdown_num==3" class="dropdown">
              <span @click="mobile_menu_opened=false"><router-link to="/msds/msds1" class="btn" :class="{active : page_sub=='MSDS의작성및제출'}">MSDS의 작성 및 제출</router-link></span>
              <span @click="mobile_menu_opened=false"><router-link to="/msds/msds2" class="btn" :class="{active : page_sub=='MSDS의일부비공개승인'}">MSDS의 일부 비공개 승인</router-link></span>
            </div>
          </transition>
        </div>
        <div class="btn-wrap" :class="{opened: dropdown_num==4}" @mouseover="dropdown_num=4" @mouseleave="dropdown_num=-1">
          <span class="name" :class="{active : page_title=='국내외등록'}">국내외 등록</span>
          <img src="@/assets/img/icon_down_arrow.png" />
          <transition name="dropdown">
            <div v-if="dropdown_num==4" class="dropdown">
              <span @click="mobile_menu_opened=false"><router-link to="/registration/registration1" class="btn" :class="{active : page_sub=='산안법신규화학물질'}">산안법 신규화학물질</router-link></span>
              <span @click="mobile_menu_opened=false"><router-link to="/registration/registration2" class="btn" :class="{active : page_sub=='국외등록'}">국외 등록</router-link></span>
              <span @click="mobile_menu_opened=false"><router-link to="/registration/registration3" class="btn" :class="{active : page_sub=='기타서비스'}">기타 서비스</router-link></span>
            </div>
          </transition>
        </div>
        <div class="btn-wrap" :class="{opened: dropdown_num==5}" @mouseover="dropdown_num=5" @mouseleave="dropdown_num=-1">
          <span class="name" :class="{active : page_title=='커뮤니티'||page_title=='포스트'}">커뮤니티</span>
          <img src="@/assets/img/icon_down_arrow.png" />
          <transition name="dropdown">
            <div v-if="dropdown_num==5" class="dropdown">
              <span @click="mobile_menu_opened=false"><router-link to="/community/community1" class="btn" :class="{active : page_sub=='공지사항'}">공지사항</router-link></span>
              <span @click="mobile_menu_opened=false"><router-link to="/community/community2" class="btn" :class="{active : page_sub=='자료실'}">자료실</router-link></span>
            </div>
          </transition>
        </div>
      </div>
      <div class="menu-btn" @click="mobile_menu_opened=!mobile_menu_opened">
        <div class="bar1"></div>
        <div class="bar2"></div>
        <div class="bar3"></div>
      </div>
    </div>
    <transition name="fade" mode="out-in">
      <router-view :sub_title="page_sub"/>
    </transition>
    <div class="footer" v-if="page_title != 'admin'">
      <router-link to="/"><img class="logo" src="@/assets/img/logo_img_v_w.png" /></router-link>
      <div class="text-area">
        <div class="row">
          <span class="txt">서울특별시 구로구 디지털로30길 31 코오롱디지털타워빌란트2차 1405호</span>
          <span class="txt">TEL: 02-6964-7119</span>
          <span class="txt">FAX: 02-867-7079</span>
        </div>
        <div class="row">
          <span class="txt">상호 | (주)케미파트너</span>
          <span class="txt">대표자 | 송은경</span>
          <span class="txt">사업자등록번호 | 121-86-41700</span>
        </div>
        <div class="row-bar"></div>
        <div class="row">
          <span class="txt">Copyright ⓒ 2020 (주)케미파트너 All Right Reserved.</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'App',
  data() {
    return {
      navbackground: false,
      page_title: '',
      page_sub: '',
      mobile_menu_opened: false,
      dropdown_num: -1,
    }
  },
  created() {
    this.page_title = this.$route.matched[this.$route.matched.length-1].meta.title;
    this.page_sub = this.$route.matched[this.$route.matched.length-1].meta.sub;
  },
  mounted () {
    window.addEventListener('scroll', this.onScroll)
  },
  beforeDestroy () {
    window.removeEventListener('scroll', this.onScroll)
  },
  methods: {
    'onScroll' : function() {
      this.navbackground=document.documentElement.scrollTop>0;
    }
  },
  watch: {
    '$route' (to, from) {
      this.page_title = to.meta.title;
      this.page_sub = to.meta.sub;
    }
  }
}
</script>

<style src="@/assets/css/style.css">
</style>



