<template>
    <div class="contents-box">
        <div class="title-wrap">
            <div class="bar"></div>
            <h1 class="title">물질안전보건자료(MSDS)의 작성 및 제출</h1>
        </div>
        <div class="item-wrap">
            <p class="text">산업안전보건법(이하, 산안법)에 따라 화학물질 또는 이를 함유한 혼합물을 제조하거나 수입하려는 자는 물질안전보건자료(이하, MSDS)를 작성하여 고용노동부장관에게 제출하여야 합니다(2021년 1월 16일 시행).</p>
            <div class="img-row">
                <img class="img12" src="@/assets/img/chemilaw_img12.png" />
            </div>
            <div class="item-box" style="margin-top: 25px;">
                <div class="row">
                    <List 
                        title="제공 서비스"
                        :contents="[
                            'MSDS 작성 컨설팅',
                            '제조, 수입, 내수구매 제품 MSDS 검증',
                            'MSDS 안전보건공단 제출 대행'
                        ]"
                    />
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import List from './StyledList';
export default {
    name: 'MSDS1',
    components: {
        List
    }
}
</script>



