<template>
    <div class="contents_wrap">
        <div class="header">
            <div class="cover">
                <div class="location_wrap">
                    <h1 class="title">
                        <span v-if="sub_title=='MSDS의작성및제출'">MSDS의 작성 및 제출</span>
                        <span v-else-if="sub_title=='MSDS의일부비공개승인'">MSDS의 일부 비공개 승인</span>
                    </h1>
                    <div class="bar">
                        <div class="point_bar"></div>
                    </div>
                    <span class="location">MSDS ＞
                        <span v-if="sub_title=='MSDS의작성및제출'">MSDS의 작성 및 제출</span>
                        <span v-else-if="sub_title=='MSDS의일부비공개승인'">MSDS의 일부 비공개 승인</span>
                    </span>
                </div>
            </div>
        </div>
        <div class="contents">
            <div class="sidebar">
                <div class="sidebar-box">
                    <router-link to="/msds"><h1 class="title">MSDS</h1></router-link>
                    <div class="bar">
                        <div class="bold-bar"></div>
                    </div>
                    <div class="navbtns">
                        <router-link to="/msds/msds1" class="btn" :class="{active : sub_title=='MSDS의작성및제출'}">MSDS의 작성 및 제출</router-link>
                        <router-link to="/msds/msds2" class="btn" :class="{active : sub_title=='MSDS의일부비공개승인'}">MSDS의 일부 비공개 승인</router-link>
                    </div>
                </div>
            </div>
            <transition name="fade" mode="out-in">
                <router-view />
            </transition>
        </div>
    </div>
</template>
<script>
export default {
    name: 'IntroducePage',
    props: ['title', 'sub_title']
}
</script>



