<template>
    <div class="contents_wrap">
        <div class="header">
            <div class="cover">
                <div class="location_wrap">
                    <h1 class="title">
                        <span v-if="sub_title=='산안법신규화학물질'">산안법 신규화학물질</span>
                        <span v-else-if="sub_title=='국외등록'">국외 등록</span>
                        <span v-else-if="sub_title=='기타서비스'">기타 서비스</span>
                    </h1>
                    <div class="bar">
                        <div class="point_bar"></div>
                    </div>
                    <span class="location">국내외 등록 ＞
                        <span v-if="sub_title=='산안법신규화학물질'">산안법 신규화학물질</span>
                        <span v-else-if="sub_title=='국외등록'">국외 등록</span>
                        <span v-else-if="sub_title=='기타서비스'">기타 서비스</span>
                    </span>
                </div>
            </div>
        </div>
        <div class="contents">
            <div class="sidebar">
                <div class="sidebar-box">
                    <router-link to="/registration"><h1 class="title">국내외 등록</h1></router-link>
                    <div class="bar">
                        <div class="bold-bar"></div>
                    </div>
                    <div class="navbtns">
                        <router-link to="/registration/registration1" class="btn" :class="{active : sub_title=='산안법신규화학물질'}">산안법 신규화학물질</router-link>
                        <router-link to="/registration/registration2" class="btn" :class="{active : sub_title=='국외등록'}">국외 등록</router-link>
                        <router-link to="/registration/registration3" class="btn" :class="{active : sub_title=='기타서비스'}">기타 서비스</router-link>
                    </div>
                </div>
            </div>
            <transition name="fade" mode="out-in">
                <router-view />
            </transition>
        </div>
    </div>
</template>
<script>
export default {
    name: 'IntroducePage',
    props: ['title', 'sub_title']
}
</script>



