<template>
    <div class="contents-box">
        <div class="title-wrap">
            <div class="bar"></div>
            <h1 class="title">신규화학물질 신고ㆍ등록</h1>
        </div>
        <div class="item-wrap">
            <p class="text">신규화학물질을 제조ㆍ수입하려는 자는 제조 또는 수입 전에 연간 제조ㆍ수입량에 따라 100킬로그램 미만은 신고를, 100킬로그램 이상은 등록을 하여야 합니다.</p>
            <div class="img-row">
                <img class="img6" src="@/assets/img/chemilaw_img6.png" />
            </div>
            <div class="item-box">
                <div class="row">
                    <List 
                        title="제공 서비스" 
                        :contents="[
                            '신고ㆍ등록대상 여부 판단',
                            '신규화학물질 신고ㆍ등록',
                            '시험의뢰 대행 및 모니터링',
                            '유일대리인(OR)',
                        ]"
                    />
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import List from './StyledList';
export default {
    name: 'Chemistrylaw4',
    components: {
        List
    }
}
</script>



